	import investorItem from '@/components/investor-item/investor-item.vue'
	import projectItem from '@/components/project-item/project-item.vue'
	import { colPersonList, collectionList } from '@/common/vmeitime-http/index.js'
    export default {
		components:{
			investorItem,
			projectItem
		},
        data() {
            return {
				loadMoreText: "加载中...",
				showLoadMore: false,
				list: [],
				pages: 0,
				projectType: '0',
				params:{
					pageSize: 15,
					pageNum: 1
				}
            }
        },
		onPullDownRefresh() {
			this.refresh()
			setTimeout(function () {
				uni.stopPullDownRefresh();
			}, 1000);
		},
		onReachBottom(){
			console.log('next');
			this.scrolltolower()
		},
		onShow(){
			let sortType = uni.getStorageSync('projectSort')
			if(sortType){
				this.params.sortType = sortType
				uni.clearStorageSync('projectSort')
				this.refresh()
			}
		},
		onLoad() {
			this.refresh()
		},
        methods: {
			// 名片列表
			async collectionList(){
				
				let res = null;
				if (this.projectType === '0') {
					res = await colPersonList({
						...this.params
					})
				} else {
					res = await collectionList({
						...this.params
					})
					res.data.data.list.forEach((item) => {
						item.fileUrl = item.projectLogo
					})
				}
				
				this.showLoadMore = false
				
				if(res.data.flag){
					this.pages = res.data.data.pages
					this.list = this.list.concat(res.data.data.list)
				}
				
			},
			refresh(){
				this.list=[]
				this.params = {
					...this.params,
					pageSize:15,
					pageNum:1
				}
				this.collectionList()
			},
			changeType(value){
				if (this.projectType === value) return;
				this.list = []
				this.projectType = value
				this.params = {
					...this.params,
					pageNum:1
				}
				this.collectionList()
			},
			// 分页
			scrolltolower() {
				if(this.params.pageNum >= this.pages || this.showLoadMore) return
				this.showLoadMore = true;
				this.params.pageNum++
				this.collectionList()
			}
        }
    }
